<template>
  <v-container>
    <v-layout text-center wrap class="d-inline-flex ml-1" max-width="800">
      <v-flex xs12>
        <v-img :src="require('../assets/logo.svg')" class="my-3" contain height="200"></v-img>
      </v-flex>

      <v-flex mb-4>
        <h1 class="display-2 font-weight-bold mb-3">Welcome to Sacrebleu</h1>
      </v-flex>

      <v-flex mb-5 xs12>
        <h2 class="headline font-weight-bold mb-3">lettersStack</h2>

        <v-layout justify-center>
          <div v-for="(letterGroup, i) in lettersStack" :key="i" class="subheading mx-3">
            <span v-for="(letter, j) in letterGroup" :key="j">{{ letter }}</span>
          </div>
        </v-layout>
      </v-flex>

      <v-flex mb-5 xs12>
        <h2 class="headline font-weight-bold mb-3">currentHand</h2>

        <v-layout justify-center>
          <v-btn small color="primary" @click="onDrawLetters">Next</v-btn>
          <v-btn small color="secondary" @click="onRestart">Restart</v-btn>

          <span v-for="(letter, i) in currentHand" :key="i" class="subheading mx-3">{{ letter }}</span>
        </v-layout>
      </v-flex>
      <v-flex mb-5 xs12>
        <h2 class="headline font-weight-bold mb-3">Put letters</h2>

        <v-layout justify-center>
          <v-form :lazy-validation="true">
            <draggable v-model="currentHand">
              <transition-group>
                <v-checkbox
                  class="d-inline-flex pa-6 letter"
                  v-for="(letter, i) in currentHand"
                  :key="'letter'+i+'_'+letter"
                  v-model="selectedLetters"
                  :label="letter"
                  :value="letter"
                >{{ letter }}</v-checkbox>
              </transition-group>
            </draggable>

            <div class="justify-center ml-12">
              <v-btn
                class="d-block ml-12"
                small
                color="secondary"
                @click="onPlay"
                :disabled="canPlay"
              >Play</v-btn>
            </div>
          </v-form>
        </v-layout>
      </v-flex>

      <v-flex mb-5 xs12>
        <h2 class="headline font-weight-bold mb-3">Words</h2>
        <v-layout justify-center>
          <p>
            <span
              v-for="(word, i) in wordsStack"
              :key="i"
              class="subheading mx-3 d-block"
            >{{ word.join('') }} ✔️</span>
          </p>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import generator from "./generator.js";
import draggable from "vuedraggable";

export default {
  name: "Sacrebleu",
  components: {
    draggable
  },
  data: () => ({
    letters: [],
    lettersStack: generator.getChunks(generator.alphabet, 7),
    wordsStack: [],

    currentChunk: 0,
    letterInput: {
      value: ""
    },
    selectedLetters: []
  }),
  computed: {
    currentHand: {
      get: function() {
        return this.letters;
      },
      set: function(newEl) {
        this.letters = newEl;
      }
    },
    canPlay() {
      let len = this.selectedLetters.length;

      console.log("canPLay len", len, this.letters.length);

      return len < 1 || len > 7 || this.letters.length === 0;
    }
  },
  methods: {
    onDrawLetters() {
      //compute chunk from avalaible letters slot
      this.lettersStack = generator.getChunks(
        _.concat(...this.lettersStack),
        7 - this.letters.length
      );

      //move new letter to current hand
      this.letters.push(...this.lettersStack[0]);
      //remove first chunk from stack
      this.lettersStack.shift();
    },
    onRestart() {
      this.lettersStack = generator.getChunks(generator.alphabet, 7);
      this.letters = [];
      this.wordsStack = [];
      this.selectedLetters = [];
    },
    onPlay() {
      //console.log("onPlay", this.selectedLetters);

      let difference = _.difference(this.letters, this.selectedLetters);

      let wordToWrite = this.selectedLetters;

      this.letters = difference;
      // this.lettersStack[0] = difference;

      //console.log("onPlay difference", difference);

      this.wordsStack.push(wordToWrite);
      this.selectedLetters = [];
    }
  }
};
</script>
<style scoped>
.letter {
  border: 1px dotted darkcyan;
}
</style>