let _ = require('lodash');
const alphabet = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z'];


export default {
    alphabet,
    getChunks: (arr, current) => {
        let res = _.chunk(arr, current);
        return res;
    },
    concatChunks: (arr) => {
        let res = arr;//_.chunk(arr, 7);
        res.shift();
        return _.concat(res);
    }
}